* {
    box-sizing: border-box;
  }
  html,
  body,
  #root {
    width: 100%;
    height: 100%;
    margin: 0;
    padding: 0;
    overflow-x: hidden;
    background-color: rgba(0, 0, 0, 0.309);
  }
  @media screen and (min-width: 600px)  {
    .mobile-break { display: none; }
  }
  body {
    display:grid;
    justify-content:center;
    align-content:center;
    min-height:100vh;
    font-family: Sulphur Point,sans-serif;
    font-weight: 300;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background: #000a13;
    transition: 0.4s ease-in-out;
  }
  .screen {
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    
  }
  .main {
    display: flex;
    z-index: 99;
    width: 100%;
    padding: 10px 5px 20px 5px;
    flex-flow: column;
    align-items: center;
    justify-content: space-around;
    
  }
  .loading {
    position: absolute;
    top: 0px;
    left: 0px;
    width: 100%;
    height: 100%;
    background: #171717;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1000;
  }
  .loading-bar-container {
    width: 100px;
    height: 32px;
    background: #272727;
  }
  .loading-bar {
    height: 32px;
    background: #f15946;
  }
  
  .drei-container {
    margin: 0 auto;
    width: 100%;
    max-width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  h1, h2, h3 {
    font-family: 'Courier New', Courier, monospace;
    font-weight: 100;
    text-align: center;
    color: whitesmoke;
  }
  h1 {
    font-size: 35px;
  }
  h2 {
    font-size: 25px;
  }
  
  p {
    font-family: monospace;
    font-weight: 200;
    color: whitesmoke;
    text-align: justify;
    font-size: 11px;
  }
  ul {
    display: flex;
    grid-auto-flow: row;
    grid-gap: 24px;
    justify-items: center;
    margin: auto;
  }
  /* below is just for demo styling */
  
  .footer {
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 100px;
    width: 100%;
    line-height: 1.3;
    font-family: Menlo, monospace;
  }
  
  @media (min-width: 500px) {
    ul {
      grid-auto-flow: column;
    }
  }
  
  a {
    color: white;
  }
  
  a:hover {
    box-shadow: inset 0 -1.2em 0 hsla(0, 0%, 100%, 0.4);
  }
  
  li:last-child {
    grid-column: 1 / 2;
    grid-row: 1 / 2;
  }
  
  li:hover ~ li p {
    animation: wave-animation 0.3s infinite;
  }
  
  @keyframes wave-animation {
    0%,
    100% {
      transform: rotate(0deg);
    }
    25% {
      transform: rotate(20deg);
    }
    75% {
      transform: rotate(-15deg);
    }
  }
  
  .canvas {
    position: fixed;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
  }
  .row {
    display: flex;
    flex-flow: row;
    align-items: center;
    text-align: center;
  }
  .col {
    margin-bottom: 5px;
    padding: 10px;
  }
  .home__hero-section {
    color: #fff;
    padding: 150px 0;
  }
  .home__hero-section-sm {
    color: #fff;
    padding: 100px 0;
  }
  .home__hero-row {
    align-items: center;
  }
  .home__hero-text-wrapper {
    max-width: 540px;
    padding-top: 0;
    padding-bottom: 30px;
  }
  .darkBg {
    background: radial-gradient(ellipse at bottom, #1b2735 0%,#000000 100%);
    height: 100vh;
    overflow: hidden;
  }
  .container {
    display: flex;
    flex: 0;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: none;
    width: 75vw;
    background-image: none;
    background-size: cover;
    background-position: center;
  }
  .cosmosapiens-wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    z-index: 99;
  }
  .cosmosapiens-container {
    border: 2px solid black;
    width: 80vh;
  }
  .token__wrapper {
    margin: 0 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .token__section {
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
  .section {
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
  .token__container-card {
    background: linear-gradient(25deg, #806f3a5e 0%, #d9bd623a 100%);
    border: 1px solid #011030;
    border-color: #d9bd62;
    box-shadow: 0 6px 20px rgba(56, 125, 255, 0.2);
    padding: 25px;
    width: 75vw;
    height: fit-content;
    text-decoration: none;
    border-radius: 4px;
    display: flex;
    flex-flow: column;
    align-items: center;
  }
  
  .token__container-section {
    width: 50vw;

  }
  .token__container {
    display: flex;
    justify-content: center;
    align-items: center;
    
    
  }
  .token__wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 25 auto;
  }
  .token__container-cardInfo {
    display: flex;
    flex-direction: column;
    height: fit-content;
    padding: 2px;
    align-items: center;
    color: #fff;
    width: 75vw;
  }
  @media screen and (max-width: 960px) {
    .token__container {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      width: 100%;
      padding: 2px 5px 0px 5px;
    }
    .token__container-card {
      width: 92vw;
      height: fit-content;
    }
    .token__wrapper {
      margin: 0 20px;
      display: flex;
      flex-direction: column;
      align-items: center;
    }
    .row {
      flex-flow: column;
      align-items: center;
    }
    .token__container-section {
      width: 75vw;
    }
  
  }
  
  .text-container {
    display: flex;
    flex: 0;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    width: 100%;
  }
  .top-line {
    width: inherit;
    color: #f00946;
    font-size: 14px;
    font-weight: 1000;
    margin-bottom: 5px;
  }
  
  @media screen and (max-width: 991px) {
    .container {
      padding-right: 10px;
      padding-left: 10px;
      width: 100vw;
    }
  }
  
  @media screen and (max-width: 768px) {
    .home__hero-text-wrapper {
      padding-bottom: 65px;
    }
  
    .col {
  
      max-width: 100%;
      flex-basis: 100%;
    }
    .responsive-wraper{
