    }
  }
  .bottom-space {
    margin-bottom: 100px;
  }
  .bottom-space-md {
    margin-bottom: 50px;
  }
  .bottom-space-sm {
    margin-bottom: 20px;
  }
  .bottom-space-xsm {
    margin-bottom: 5px;
  }
  .bottom-space-xxsm {
    margin-bottom: 0px;
  }
  .bottom-space-xxxsm {
    margin-bottom: -5px;
  }
  
  .btn {
    text-decoration: none;
    border: 1px solid rgb(146, 148, 248);
    position: relative;
    overflow: hidden;
    border-radius: 5px;
    font-size: 1.25rem;
    min-height: 30px;
    padding:  0.5rem 7rem; 
    margin: 10px;
    color: #f4f4f4;
    text-transform: uppercase;
    display: flex;
    flex-flow: row;
    align-items: center;
  }
  
  .btn:hover {
    box-shadow: 1px 1px 25px 10px rgba(146, 148, 248, 0.4);
  }
  
  .btn:before {
    content: "";
    position: absolute;
    top: 0;
    left: -100%;
    width: 100%;
    height: 100%;
    background: linear-gradient(
      120deg,
      transparent,
      rgba(146, 148, 248, 0.4),
      transparent
    );
    transition: all 650ms;
  }
  
  .btn:hover:before {
    left: 100%;
  }
  
  
  .nav {
    width: 60vw;
    margin: 10px 0px 10px 0px;
    background: linear-gradient(25deg, #806f3a5e 0%, #d9bd623a 100%);
    border: 1px solid black;
    border-radius: 15px;
    border-color: #d9bd62;
    box-shadow: 0px 4px 0px -2px rgba(0, 0, 0, 0.3);
    height: 150px;
    display: flex;
    flex-flow: column;
    justify-content: center;
    align-items: center;
    top: 0;
    z-index: 999;
    font-family: Menlo, monospace;
  }
  
  .navbar-container {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  
  
  $easeInOutQuad: cubic-bezier(0.455, 0.030, 0.515, 0.955);
  .navbar-logo {
    color:white;
    font-size:6em;
    text-align:center;
    animation: neonGlow 0.5s infinite alternate $easeInOutQuad;
  }
  @keyframes neonGlow {
    0% {
      text-shadow: 0 0 10px rgba(255,255,255,.8),
                   0 0 20px rgba(255,255,255,.8),
                   0 0 22px  rgba(255,255,255,.8),
                   0 0 40px  rgba(66,220,219,.8),
                   0 0 60px  rgba(66,220,219,.8),
                   0 0 80px  rgba(66,220,219,.5),
                   0 0 100px rgba(66,220,219,.5),
                   0 0 140px rgba(66,220,219,.5),
                   0 0 200px rgba(66,220,219,.5);
    }
    100% {
      text-shadow: 0 0 2px rgba(255,255,255,.8),
                   0 0 8px rgba(255,255,255,.8),
                   0 0 10px rgba(255,255,255,.8),
                   0 0 20px rgba(66,220,219,.8),
                   0 0 30px rgba(66,220,219,.8),
                   0 0 40px rgba(66,220,219,.8),
                   0 0 50px rgba(66,220,219,.5),
                   0 0 80px rgba(66,220,219,.5);
    }
  }
  
  .navbar-icon {
    margin-right: 0.5rem;
  }
  
  .nav-menu {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    list-style: none;
    text-align: center;
  }
  
  .nav-item {
    border-bottom: 2px solid transparent;
  }
  
  .nav-item:hover {
    border-bottom: 2px solid #f00946;
  }
  
  .nav-links {
    color: #fff;
    display: flex;
    align-items: center;
    text-decoration: none;
    padding: 0.5rem 1rem;
    height: 100%;
  }
  
  .fa-bars {
    color: #fff;
  }
  
  .menu-icon {
    display: none;
  }
  
  @media screen and (max-width: 960px) {
    .NavbarItems {
      position: relative;
    }
    .navbar-logo {
      font-size: 5.5em;
    }
  
    .nav-menu {
      display: flex;
      flex-direction: column;
      width: 100%;
      height: 80vh;
      position: absolute;
      top: 80px;
      left: -100%;
      opacity: 1;
      transition: all 0.5s ease;
    }
    .nav {
      width: 95vw;
      height: 100px;
    }
  
    .nav-menu.active {
      left: 0;
      opacity: 1;
      transition: all 0.6s ease;
      z-index: 1;
      top: 150px;
      height: 50vh;
    }
  
    .nav-links {
      text-align: center;
      padding: 2rem;
      width: 100%;
      display: table;
    }
  
    .nav-links:hover {
      color: #f00946;
      transform: scale(1.2);
      transition: all 0.3s ease;
    }
  
    .nav-item:hover {
      border: none;
    }
  
    .nav-item {
      width: 100%;
    }
  
    .img {
      position: absolute;
      top: 0;
      left: 0;
      max-width: 5%;
      max-height: 5%;
    }
  
    .menu-icon {
      display: block;
      position: absolute;
      top: 0;
      right: 0;
      transform: translate(-100%, 60%);
      font-size: 1.8rem;
      cursor: pointer;
    }
  
    .fa-times {
      color: #fff;
      font-size: 2rem;
    }
  
    .nav-btn {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 100%;
      height: 120px;
    }
  }
  
  /* ROAD MAP */
  //Text Styles 
  @import url("https://fonts.googleapis.com/css?family=Open+Sans:400,400i,700");
  $font-sans: 'Open Sans', sans-serif;
  
  //Colors
  $black: #34435E;
  $white: #ffffff;
  
  .roadmap {
    font-size: 1em;
    font-family: monospace;
    font-weight: 200;
    text-align: center;
    color: whitesmoke;
    line-height: 1.75em;
    border-top: 3px solid;
    border-image: linear-gradient(to right, #443ad580 0%, #3ab1d575 100%);
    border-image-slice: 1;
    border-width: 2px;
    margin: 0;
    padding: 40px;
    counter-increment: section;
    position: relative;
    //numbers
    &:before {
      content: 'phase 'counter(section);
      position: absolute;
      border-radius: 5%;
      padding: 6px;
      height: 2.25em;
      width: 2.5em;
      background-color: rgba(0, 0, 0, 0.6);
      border: 1px solid black;
      text-align: center;
      line-height: 1.25em;
      color: $white;
      font-size: 1.0em;
    }
  }
  //odd number borders
  .roadmap:nth-child(odd) {
    border-right: 3px solid;
    padding-left: 0;
    
    &:before {
      left: 100%; 
      margin-left: -20px;
    }
  }
  //even number borders
  .roadmap:nth-child(even) {
    border-left: 3px solid;
    padding-right: 0;
    
    &:before {
      right: 100%; 
      margin-right: -15px;
    }
  }
  //handle first and last
  .roadmap:first-child {
    border-top: 0;
    border-top-right-radius:0;
    border-top-left-radius:0;
  }
  .roadmap:last-child {
    border-bottom-right-radius:0;
    border-bottom-left-radius:0;
  }
